import Vue from 'vue'
import App from './App.vue'
import element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueRouter from 'vue-router'
import Main from './components/Main.vue'
import Work from './components/compoents2/Work.vue'
import Reader from './components/compoents2/Reader.vue'
import Book from './components/compoents2/Book.vue'
import Register from './components/compoents2/Register.vue'

Vue.use(element)
Vue.use(VueRouter)


Vue.config.productionTip = false

const routes = [
  {
    path:"/",
    component:Main
  },
  {
    path: '/login',
    component:Main
  },{
    path: '/work',
    component:Work
  },{
    path: '/reader',
    component:Reader
  },
  {
    path: '/book',
    component:Book
  },
  {
    path: '/register',
    component:Register
  }
]
const router = new VueRouter({routes})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
